$bg_color: rgb(159, 159, 159);
$shadow_color: rgba(59, 59, 60, 0.632);
$shape_color_pri: rgb(121, 164, 181);
$shape_color_sec: rgb(192, 196, 145);
$heading_font_color: rgb(38, 38, 37);
$text_font_color: rgb(27, 27, 27);
$intro_font: 'Merriweather Sans', sans-serif;
$heading_font: 'Exo', sans-serif; 
$text-font: 'Proza Libre', sans-serif;

// ************* INTRO *************

.intro-container {
   display: grid;
   grid-template-columns: 1fr repeat(6, 2fr);
   grid-template-rows: repeat(12, 8%);
   font-family: $intro-font;
   color: $heading-font-color;
   justify-items: center;
   align-items: center;
   height: 100vh;
   width: 100vw;
   overflow: hidden;
}

.pic-container {
   grid-area: 1 / 3 / 8 / 8;
   width: 90%;
   height: 90%;
}

.profile-pic {
   width: 100%;
}

.intro-name {
   grid-area: 3 / 1 / 6 / 4;
   padding: 0 5rem 0.6rem 1rem;
   border-radius: 10px;
   background: transparent;
}

.intro-name h2 {
   background: transparent;
   font-size: 3.5vw;
}

.intro-name h1 {
   font-size: 5vw;
   background: transparent;
   padding-left: 10%;
}

.intro-airbnb {
   grid-area: 5 / 2 / 9 / 4;
   background: transparent;
}

.airbnb-png {
   width: 15vw;
   background: transparent;
   padding-left: 10%;
}

.intro-super {
   grid-area: 7 / 2 / 11 / 4;
   font-size: 2.5vw;
   border: 1px solid black;
   padding: 1.5rem 3rem;
   transform: skew(-10deg);
   margin-left: -5rem;
}

.intro-super h1 {
   background: transparent;
}

.intro-ambass {
   grid-area: 9 / 2 / 13 / 4;
   font-size: 2.5vw;
   border: 1px solid black;
   padding: 1.5rem 3rem;
   transform: skew(-10deg);
   margin-left: 25%;
}

.intro-ambass h1 {
   background: transparent;
}


// ************* ABOUT *************

.about-container {
   display: grid;
   grid-template-columns: repeat(4, 2fr) 1fr;
   grid-template-rows: repeat(2, 1fr);
   border-top: 2px solid black;
}

.decorative-container {
   width: 100%;
   height: 100%;
   overflow: hidden;
   position: relative;
   grid-area: 1 / 3 / 3 / 6;
   z-index: 5;
   background: transparent;
}

.decorative-container::before {
   content: "";
   display: block;
   width: 200%;
   height: 200%;
   position: absolute;
   border-radius: 50%;
   top: 0;
   right: 0;
   box-shadow: 500px -500px 0 0 black;
}

.bathroom-img {
   background: transparent;
   grid-area: 1 / 1 / 4 / 3;
}

.bathroom-img img {
   width: 80%;
   z-index: 1;
   padding: 10%;
}

.about-me-text {
   grid-area: 1 / 3 / 3 / 5;
   padding-top: 10%;
   background: transparent;
   z-index: 1;
}

.about-me-text h1 {
   font-family: $heading-font;
   font-size: 3rem;
}

.about-me-text hr {
   margin-bottom: 3%;
   width: 70%;
}

.about-me-text p {
   grid-area: 2 / 3 / 3 / 4;
   z-index: 1;
   background: transparent;
   font-family: $text-font;
   font-size: 1.2rem;
   width: 70%;
}


// ************* SERVICES *************

.text a {
   cursor: url(./pointer-click.png), pointer;
}

.services-container {
   display: grid;
   grid-template-columns: 1fr 1fr repeat(5, 2fr);
   grid-template-rows: repeat(3, 1fr);
   border-bottom: 2px solid black;
}

.decorative-container-services {
   width: 100%;
   height: 100%;
   overflow: hidden;
   position: relative;
   grid-area: 1 / 1 / 4 / 5;
   z-index: 5;
   background: transparent;
}

.decorative-container-services::before {
   content: "";
   display: block;
   width: 200%;
   height: 200%;
   position: absolute;
   border-radius: 50%;
   bottom: 0;
   left: 0;
   box-shadow: -500px 500px 0 0 black;
}

.livingrm-img {
   background: transparent;
   grid-area: 1 / 5 / 5 / 8;
}

.livingrm-img img {
   width: 80%;
   z-index: 2;
   padding: 10%;
}

.services-text {
   grid-area: 1 / 1 / 3 / 6;
   padding: 10% 0 0 25%;
   background: transparent;
   z-index: 1;
}

.services-text h1 {
   font-family: $heading-font;
   font-size: 3rem;
   background: transparent;
}

.services-text hr {
   margin-bottom: 3%;
   width: 70%;
}

.services-text p {
   grid-area: 2 / 3 / 3 / 4;
   z-index: 1;
   background: transparent;
   font-family: $text-font;
   font-size: 1.2rem;
   width: 70%;
}

// ************* BUTTON *************

@mixin btn__split($shape-color-pri, $shape-color-sec, $text-font-color) {
   .btn {
      position: relative;
      letter-spacing: 0.25em;
      margin: 0 auto;
      padding: 1rem 2.5rem;
      background: transparent;
      outline: none;
      font-size: 3rem;
      color: $text-font-color;
      z-index: 1;
   
      &::after,
      &::before {
         content: "";
         position: absolute;
         height: 100%;
         width: 100%;
         transform: skewX(30deg);
         transition: all 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55);
         z-index: -2;
      }
      &::before {
         background-color: $shape-color-pri;
         top: -1rem;
         left: -3rem;
      }
      &::after {
         background-color: $shape-color-sec;
         top: 1rem;
         left: 2rem;
      }
      &:hover {
         &::before,
         &::after {
            top: 0;
            transform: skewx(0deg);
         }
         &::after {
            left: -5rem;
         }
         &::before {
            left: 6rem;
         }
      }
   }
}

.button-container {
   padding-top: 4rem;
   text-align: center;
   width: auto;
   margin: auto;
   grid-area: 3 / 5 / 4 / 8;
}

.button-container a {
   text-decoration: none;
   font-weight: 600;
   cursor: url(point-click.png), pointer;
}

@include btn__split($shape-color-pri, $shape-color-sec, $text-font-color);

.space-holder {
   height: 10rem;
}